@media screen and (max-width: 1280px) {
    .product__container {
        display: flex;
        flex-direction: column;
        background-color: #F8f7f4;
        margin: 20px 20px;
    }
    .product__container img {
        width: 100%;
        height: 100%;
    }
    .product__container h2 {
        color: rgb(56, 56, 56);
        padding-left: 25px;
        margin-bottom: 5px;
    }
    .product__container h1 {
        font-size: 20px;
        font-weight: 300;
        padding-left: 25px;
        margin-bottom: 0;
    }
    .product__container h3 {
        font-size: 30px;
        font-weight: 700;
        color: rgb(250, 76, 96);
        padding-left: 25px;
        margin-top: 10px;
        margin-bottom: 10px;
    }
}
