@media screen and (min-width: 1281px) {
    .product__container {
        display: flex;
        flex-direction: column;
        background-color: #F8f7f4;
        width: 20%;
        margin: 20px;
}
    .product__container h1 {
        font-size: 20px;
        font-weight: 300;
        margin-left: 10px;
        margin-top: 0px;
        text-align: justify;
    }
    .product__container h2 {
        color: rgb(56, 56, 56);
        transition: 0.5s;
        font-size: 21px;
        margin-left: 10px;
        margin-bottom: 0px;
}
    .product__container h2:hover {
        color: rgb(252,90,109);
}
    .product__container h3 {
        font-size: 30px;
        font-weight: 700;
        color: rgb(252,90,109);
        margin: 0px 10px 10px 10px;
        text-align: end;
}
    .carousel .slider-wrapper img {
        width: 100%;
        object-fit: contain;
        background-color: rgb(51, 51, 51);
    }
    .carousel .thumbs li{
        height: 100px;
    }
    .carousel .thumbs img {
        object-fit: contain;
    }
    
}
