@media screen and (max-width: 480px) {
    .dtf__container {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;        
    }

    .dtf__image {
        width: 300px;
        overflow: hidden;
        margin: 30px;
    }

    .dtf__image img {
        width: 300px;
        transition: transform .7s;
        padding-top: 10px;
    }

    .dtf__image img:hover {
        transform: scale(1.5);
    }

    .dtf__wrapper h2 {
        font-size: 15px;
        text-align: justify;
        font-weight: 300;
        margin: 25px;
    }

    .dtf__wrapper h1 {
        font-size: 20px;
        padding-left: 20px;
        color: rgb(252,90,109);
    }

    .dtf__wrapper ul li {
        color: black;
        list-style: none;
        margin: 10px;
        border-left: 3px solid rgb(252,90,109);
        padding-left: 5px;
        font-weight: 300;
    }
  }

  @media screen and (min-width: 481px) and (max-width: 1000px) {
    .dtf__container {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;        
    }
    .dtf__image {
        width: 350px;
        overflow: hidden;
        margin: 30px;
    }
    .dtf__image img {
        width: 350px;
        transition: transform .7s;
    }
    .dtf__image img:hover {
        transform: scale(1.5);
    }
    .dtf__wrapper h2 {
        font-size: 15px;
        text-align: justify;
        font-weight: 300;
        margin: 25px;
    }
    .dtf__wrapper h1 {
        font-size: 20px;
        padding-left: 20px;
        color: rgb(252,90,109);
    }
    .dtf__wrapper ul li {
        color: black;
        list-style: none;
        margin: 10px;
        border-left: 3px solid rgb(252,90,109);
        padding-left: 5px;
        font-weight: 300;
    }
  }

  @media screen and (min-width: 1001px) {
    .dtf__container {
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: 50px 0px;
        flex-grow: 70%;
        align-items: center;
    }
    .dtf__title {
        display: none;
    }
    .dtf__wrapper {
        display: flex;
        flex-direction: column;
        width: 30%;
        margin: 0px 50px;
    }
    .dtf__wrapper h1 {
        font-size: 30px;
        color: rgb(252,90,109);
    }
    .dtf__wrapper h2 {
        text-align: justify;
        font-size: 15px;
        font-weight: 400;
    }
    
    .dtf__image {
        overflow: hidden;
    }
    .dtf__container img {
        transition: transform .7s;
    }
    
    .dtf__container img:hover {
        transform: scale(1.5);
    }

    .dtf__wrapper ul {
        position: relative;
    }
    
    .dtf__wrapper ul li {
        position: relative;
        left: 0;
        color: black;
        list-style: none;
        margin: 4px 0;
        border-left: 3px solid rgb(252,90,109);
        transition: 0.5s;
        cursor: pointer;
    }

    .dtf__wrapper ul li:hover {
        left: 10px;
    }

    .dtf__wrapper ul li span {
        position: relative;
        padding: 8px;
        padding-left: 12px;
        display: inline-block;
        z-index: 1;
        transition: 0.5s;
      }

      .dtf__wrapper ul li:hover span {
        color: #111;
      }

      .dtf__wrapper ul li:before {
        content: "";
        position: absolute;
        width: 100%;
        background: rgb(252,90,109);
        transform: scaleX(0);
        transform-origin: left;
        transition: 0.5s;
      }

      .dtf__wrapper ul li:hover:before {
        transform: scaleX(1);
      }

      .dtf__wrapper h4 {
        text-align: center;
        margin: 5px;
        font-weight: 400;
      }
  }