
@media screen and (max-width: 430px) {
    .promotedcard__container {
        background-color: rgb(63, 60, 60);
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .promotedcard__products {
        display: flex;
        justify-content: center;
        flex-direction: column;
        flex-wrap: wrap;
    }
}
@media screen and (min-width: 431px) {
    .promotedcard__container {
        background-color: rgb(63, 60, 60);
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .promotedcard__products {
        display: flex;
        justify-content: center;
        flex-direction: row;
        flex-wrap: wrap;
    }
    
    .promotedcard__products h4 {
        text-align: center;
        font-weight: 400;
        font-size: 17px;
    }
}
  